* {
  font-size: 16px;
}

html,
body {
  -webkit-overflow-scrolling: touch !important;
  overflow: auto !important;
  height: 100% !important;
}
body {
  min-height: 100%;
  width: 100%;
}

body.fix {
  background-color: red;
}

.app {
  padding-top: 45px;
  padding-bottom: 45px;
}

.super-sticky_ghost {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
}

.super-sticky_content {
  position: fixed;
  left: 0px;
  width: 100%;
}

.super-sticky_ghost {
  background: rgba(200, 255, 255, 0);
}
.super-sticky_content {
  background: #ffddff;
}
